* {

  font-family: "DM Sans", sans-serif;
  font-optical-sizing: auto;
  
}

body {
  background: rgb(102, 155, 255);
  font-size: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
}

hr {
  border: 1px solid white;
}

.container {
  margin-top: 20px;
  background: rgb(255, 255, 255, 0.2);
  backdrop-filter: blur(1px);
  border-radius: 10px;
  padding: 10px;
  width: 75vw;
}

.d-flex {
  display: flex;
  justify-content: center;
  align-items: center;
}

.toolbar {
  display: flex;
  justify-content: space-between;
  gap: 5px;
}
.toolbar-dashboard{
  display: flex;
  justify-content: flex-end;
  gap: 5px;
}

.toolbar-btn {
  display: inline-flex;
  gap: 5px;
}


.ml2 {
  margin-left: 5px;
}

.mr2 {
  margin-right: 5px;
}

.editbtn {
  font-size: 16px;
  cursor: pointer;
  padding: 5px 10px;
  border: none;
  background: rgb(99, 255, 94);
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border-radius: 6px;

}

.delbtn {
  border-radius: 6px;
  font-size: 16px;
  cursor: pointer;
  padding: 5px 10px;
  border: none;
  background: #ff4545;
  display: inline-flex;
  align-items: center;
  justify-content: center;
}


.btn {
  font-size: 16px;
  cursor: pointer;
  padding: 5px 10px;
  border: none;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border-radius: 6px;
  background: rgb(42, 184, 255);

}

.btn:hover {
  background-color: #222;
  color: #ccc;
}

.btn span {
  margin-left: 5px;
}

.table {
  border-collapse: collapse;
  color: rgb(8, 8, 8);
  width: 100%;
}

.table td {
  text-align: center;
}

.table tr {
  margin: 10px 0;
  box-shadow: 2px 2px -2px black;
}

.table tr:hover {
  background-color: #dadada;
}

.table th {
  border: none;
  padding: 8px;
  padding-top: 12px;
  padding-bottom: 12px;
  text-align: center;
  background-color: #dfdfdfc0;
  color: rgb(0, 0, 0);
  text-transform: uppercase;
  font-size: 15px;
}


.form label {
  width: 100%;
  margin-bottom: 10px;
}

.form input {
  width: 100%;
  margin-bottom: 10px;
  margin-top: 10px;
  height: 30px;
}

.form textarea {
  width: 100%;
  margin-top: 10px;
}

.card-table {
  display: flex;
  flex-direction: column;
}

.card-analytics {
  display: flex;
  flex-direction: row;
  gap: 20px;

}

.heading {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  gap: 20px;
}

.card {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  background: white;
  border-radius: 20px;
  height: 30vh;
  width: 50vh;
  box-shadow: 2px 2px 5px rgb(88, 88, 88);
}

.Number {
  font-size: 50px;
}

.deliverd{
  background-color: #73ff57;
  border-radius: 10px;
}
.cancelled{
  background-color: #ff5757;
  border-radius: 10px;
}


@media (max-width: 320px) {
  .container {
    width: auto;
  }
  .table {
    width: auto;
  
  }

  .card-table {
    display: flex;
    flex-direction: row;
  }
  
  .card-analytics {
    display: flex;
    flex-direction: column;
    gap: 40px;
  
  }
  
}
